import { Command } from '@ali/4ever-cangjie';
import toolbar from './toolbar';
import menubar from './menubar';
import setSize from './commands/setSize';
import { DEPRECATED_ACTIONS, actions, isActions } from './actions';
import onAction from './handlers/onAction';
import onKeyDown from './handlers/onKeyDown';
import { BiPlugin } from '../../types';
import createRenderToolbar from './createRenderToolbar';
import { queries } from './queries';

const commands: Record<string, Command> = {
  setSize,
};

function createSzPlugin(config): BiPlugin {
  const szConfig = config.sz || {};
  return {
    toolbar: toolbar(szConfig),
    renderToolbar: createRenderToolbar(szConfig.locale),
    menubar: menubar(),
    commands,
    queries,
    actions,
    isActions,
    onAction,
    onKeyDown,
    models: {
      klass: 'mark',
      type: 'sz',
    },
  };
}

createSzPlugin.actions = DEPRECATED_ACTIONS;
createSzPlugin.commands = commands;
createSzPlugin.queries = queries;

export default createSzPlugin;
